import React, { Component, useState } from "react";
import HeroeInfo from "../Components/HeroeInfo";
import axios from "axios";
import { getMD5 } from "./md5";
import Logo from "../Resources/Logo.webp";
import "./Heros.css";
import Grid from '@material-ui/core/Grid'

class Heros extends Component {
    constructor() {
        super();
        this.state = {};

        this.handleClick = this.handleClick.bind(this);
    }

    state = {
        datos: [],
        search: "",
    };

    clearAll = (event) => {
        this.setState({
            datos: [],
            search: "",
        });
    };

    handleInputChage = (event) => {
        console.log(event.target.value);
        const search = event.target.value;
        this.setState({ search });
    };

    handleClick = () => {
        const url = "https://gateway.marvel.com/v1/public/characters";
        const name = this.state.search;
        var timeStamp = Date.now();
        const privateKey = "2b34de085d28d56860618362009d438347be587f";
        const publicKey = "5a502ff3e8dcc26ff9a3a49b02000a15";

        console.log(url);
        console.log(name);
        console.log(timeStamp);
        console.log(privateKey);
        console.log(publicKey);

        var stConvert = timeStamp + privateKey + publicKey;
        var md5 = getMD5(stConvert);

        const api_query = `${url}?nameStartsWith=${name}&apikey=${publicKey}&ts=${timeStamp}&hash=${md5}`;
        console.log(api_query);

        axios
            .get(api_query)
            .then((res) => {
                const datos = res.data.data.results;

                this.setState({
                    datos,
                });
            })
            .catch((error) => {
                console.log(error);
            });
    };

    render() {
        const { datos, search } = this.state;

        return (
            <div className="o-card">
                <Grid container className="o-header">
                    <Grid md={2} sm={3} xs={6} className="o-logo">
                        <img src={Logo} />
                    </Grid>
                    <Grid md={6} sm={6} xs={12} className="o-search">
        
                    <input type="text" placeholder="Buscar un personaje..." 
                    onChange={this.handleInputChage} autocomplete="off"/>
                    <button className="o-btnSearch" onClick={this.handleClick}></button>
                    <button className="o-btnClear" onClick={this.clearAll}></button>
                    </Grid>
                    
                </Grid>
                <HeroeInfo datos={datos} />
            </div>
        );
    }
}

export default Heros;
